export default {
  name: 'coupon',
  props: {
    type: {
      type: [Number, String],
      default: 1,
    }, // 状态 1:带按钮型 2:积分兑换型
    shopName: {
      type: [String],
      default: '',
    }, // 店名
    title: {
      type: String,
      default: '',
    }, // 名称
    discount: {
      type: String,
      default: '',
    }, // 折扣
    amount: {
      type: String,
      default: '',
    }, // 金额
    unit: {
      type: String,
      default: '折',
    }, // 单位
    point: {
      type: [String, Number],
      default: '',
    }, // 积分
    rule: {
      type: String,
      default: '',
    }, // 限定
    deadline: {
      type: String,
      default: '',
    }, // 截止日期
    status: {
      type: [Number, String],
      default: 1,
    }, // 状态 1:可领取 2:已领取 3：已使用 4：已过期
  },
  computed: {
    buttonText() {
      let text = ''
      switch (parseInt(this.status)) {
        case 1:
          text = '立即领取'
          break;
        case 2:
          text = '立即使用'
          break;
        case 3:
          text = '已使用'
          break;
        case 4:
          text = '已过期'
          break;
      }
      return text
    },
  },
  methods: {
    handleClick(value) {
      switch (value) {
        case 'item':
          this.$emit('click')
          break;
        case 'button':
          this.$emit('buttonClick')
          break;
      }
    },
  },
}
